　　<p class="ptext">为实现机器学习进行知识训练，通过学习文献、电子病历、健康档案（超3000万份），覆盖病种2000+，覆盖症状5000+。目前已经辅助医生完成超过1000万次诊断。支持对比疾病诊疗知识库辅助定位疾病，提供多维度诊疗建议，也可实现医生书写病历时实时调用。</p>
