　　<p class="ptext">睿医阴道镜宫颈癌智能诊断在阴道镜不同的应用场景上实现全覆盖，采用基于生成性模型的样本扩增方法强化模型对特定干扰的耐受度可以自动识别阴道镜醋酸图和碘图，支持宫颈LSIL/HSIL病灶分割及定位的数据指标可以达到准确率：84%，敏感度：86%，特异性：83%。这在同类产品中处于领先水平。具有高标准数据输入，专家级AI能力输出，高效运算分析等优点</p></div></div>
