      <p class="contenttit">微医捐赠亿元建立浙大睿医研究中心，让医学AI打造真正的「超级医生」</p>
      <p class="ptime"><span>[雷锋网]</span><span style="margin-top: 1px;">2017-03-25</span></p>
      <img src="/static/image/news1.jpg" style="margin-bottom: 40px;" class="detailsimg">
      <div class="details">
        <p style="text-indent: 0em">导语：3 月 25 日，微医向浙江大学教育基金会捐赠一亿元，并建立浙大睿医人工智能研究中心。通过「产学研」一体化模式，构建中国首个开放式医学人工智能平台。</p>
        <p>在距离 120 周年校庆还有 57 天之际，浙江大学提前收获了一份「厚礼」——3 月 25 日，微医向浙江大学教育基金会捐赠一亿元人民币，并建立浙大睿医人工智能研究中心，通过「产学研」一体化模式，构建中国首个开放式医学人工智能平台。</p>

<p>睿医取自「real doctor」的谐音，这个名字同样赋予了微医与浙大的期待：人类医生能在人工智能的帮助下借鉴足够的数据、足够的经验、被足够的患者认同，并且在这个过程中极高地提升效率和准确度，成为「真正的超级医生」。</p>

<p>在今年被写入政府工作报告的人工智能，被浙江大学校长吴朝晖称为「医学皇冠上的明珠」。国内目前的优质医疗资源配置极不平衡，基层医疗结构与医生的服务能力不足，亟需技术化手段从根本上解决供需问题。</p>

<p>微医 CEO 廖杰远告诉雷锋网(公众号：雷锋网)，医学人工智能需要从数据源、应用场景和技术三者入手，打通医学各个学科之间的联系，并结合企业和机构的通力协作，借助源源不断的数据源训练，才有可能真正帮助医生。</p>

<p>浙江大学在 1981 年就成立了最早的人工智能研究中心之一，且是在医学、工科、信息科学少有都具备领先科研优势的综合性大学，而校长吴朝晖也同样是人工智能的顶尖专家，浙大在 AI 领域的积累和优势可谓得天独厚。睿医依托浙江大学的学术界研究实力，借助与微医等工业界的具体项目合作，兼具科研平台与产业转化平台属性。</p>

<p>浙江大学计算机学院教授吴健在采访中提及，睿医人工智能研究中心将在三个方面开展科研工作：</p>

<p>首先是基础数据的应用，通过采集日常行为数据，为用户制定更好的解决方案；</p>

<p>其次是影像学数据，推进医生辅助诊断的效率；</p>

<p>第三点是基因组数据，涉及国家战略及生物安全的研究。</p>

<p>而作为捐赠方和合作方的微医，从 7 年前的挂号网，到 2015 年乌镇互联网医院的首度亮相，再到如今的人工智能 3.0 时代转型，它在「就医指导和健康咨询平台」的道路上又做出了新的尝试。</p>

<p>微医 CEO 廖杰远告诉雷锋网，公司成立的初衷就是希望能够通过技术和创新提升中国的医疗体系效率，而微医在用挂号网、用互联网医院将人连起来后，发现更重要的是借助人工智能的力量，「把无数人的智慧凝聚成一个机器的大脑，通过机器的大脑能够替代繁杂的人工工作，成为每个医生的助手，让每个中国的医生都成为超级医生。」</p>

<p>睿医与微医的合作产品将会在微医平台上连接的 2460 家医院、26 万名医生和 1.5 亿患者和未来接入的超过 100 万家基层医疗点上运用，提供更为精准、个性化的服务。这一数字非常可观，倘若睿医真能在未来顺利落地，无疑能够让每一个患者都享受到便利的看病流程。</p>

<p>而从长远来说，医学人工智能的战略意义并不止于减轻医患就诊的供需矛盾，更重要的是建立起具有自主知识产权的国家技术壁垒。廖杰远在采访中表示：「在中国，真正的 AI 有机会帮助医生的能力、医学的能力实现质的提升，但这件事一定要有自主知识产权。如果不是自主知识产权，也不可能形成规模应用，也没有医疗机构、没有专家敢用它。医疗与健康的数据，整个人群和人种的数据，长期积累下来的安全要求最高。健康大数据的安全性至关重要，一定要建自主知识产权的医学 AI。这是安全性的问题，而且是产业基本的立足点问题。」</p>

<p>卫计委副主任金小桃也表达了对睿医成为「国家队」的期待，「希望浙大睿医人工智能研究中心，未来担负起国家健康医疗大数据应用研究院和健康产业发展的重任，在健康医疗大数据的发展和应用上发挥更加重要的正能量。」雷锋网也将持续关注睿医的进展。</p>
      </div>
      </div>

      <div v-if='newsbtn==1'>
      <p class="contenttit">浙江大学睿医人工智能研究中心主任吴健：医疗大数据呼之欲出</p>
      <p class="ptime"><span>[亿欧网]</span><span style="margin-top: 1px;">2017-04-21</span></p>
      <img src="/static/image/news2.png" style="margin-bottom: 40px;" class="detailsimg">
      <div class="details"><p>2017年4月21日，由中国人工智能学会和亿欧主办的以“大数据、AI成为传统产业的新动力”为主题的“新科技·划时代”峰会在深圳市举行。本次峰会旨在集合学会、企业、政府之力，将AI和产业融合，提升企业效率和带动产业升级。</p>

<p>出席此次峰会的嘉宾包括将门CEO高欣欣、丰元创投李强、奇点汽车战略合作总监高华、亿欧网副总裁由天宇、智童时刻创始人郭长琛等。</p>

<p>浙江大学睿医人工智能研究中心主任吴健就“医疗大数据呼之欲出”发表了演讲，以下是吴健的主要观点提炼：</p>

<p>1、医学人工智能的现状与展望；</p>

<p>2、医学人工智能的目标；</p>

<p>3、人工智能与大数据；</p>

<p>以下为吴健就“医疗大数据呼之欲出”的演讲内容整理：</p>

<p>感谢大家，刚才看了亿欧制作的人工智能30人，心潮澎湃。</p>

<p>国家对医疗大数据的人工智能有非常多的政策，去年国务院常务会议通过了《健康医疗大数据应用发展的指导意见》，国务院常务会部署了医联体，并推出了人工智能辅助诊断的技术管理规范，以及人工智能辅助诊断的临床应用指标，再有是把四个科室独立出来了，对我们来说非常好。</p>

<p>医疗大数据呼之欲出。</p>

<p>大数据的中心，去年6月份已经批准了福建省、江苏省及福州、厦门、南京、常州六个省市作为试点。</p>

<p>3月分发布的健康医疗数据公司，四个国企，如中国电子，国开投，联通等四个国企来建设大数据公司，成为医疗大数据的产业国家队。</p>

<p>国家卫计委成立多个健康医疗大数据研究院，目前正在筹建当中。</p>

<p>已经开始进行数据共享的试点，京津冀三地一百多家医院。</p>

<p>医疗领域的痛点非常多。医疗需求的入口也非常的旺盛，从院前、中、后的服务，健康诊断的，健康维护的等等非常多的需求出现，当然这个当中也已经有大量的公司在满足大家的需求。</p>

<p>健康医疗入口非常多，从健康管理自诊，导诊，候诊等等都已经有大量的企业在里面。

互联网医疗在去年、前年两年的发展中迭代非常快，下面到智慧医疗还有一点点的距离。

人工智能的工具日新月异，昨天有一个爆炸性的新闻，百度把自己的无人驾驶车的整套工具，以及数据全部都公开出来，无异于出了一个原子弹，数百亿美元的研究投入就砸了没有了。</p>

<p>人工智能工具箱Caffe2今年刚刚发布，已经不再是在大的应用上做深度学习了，而是一些小的上面也可以做轻度学习。Tensorflow，也是今年发布了。还有MXnet，也是计算非常迅速的，还有微软的工具。前面的四个都是深度学习的框架。后面的CNTK是简单的机器学习上的应用。

这样的免费工具的大量出现，使得人工智能的门槛降低得非常非常多，大家自己随便都可以来玩一下。</p>

<p>在这样的市场下，我给大家讲三点：医学人工智能的领先，可能的展望，以及我们自己做的工作。</p>

<p>去年的12月份，google发了一篇文章，几乎可以做得比医生更高的水平，拿了12.8万的数据，每个图片都有多个医生来标注，最后测试的几千张图片里面也有医生的标注，最后发现可以比真实的医生做得更好一些。

今年2月份Nature杂志上刊登了一个文章，讲斯坦福大学的皮肤癌的诊断，拿了12.9万的皮肤病的数据，其中有正常的，也有一些有疾病的数据，做了以后，人工智能的蓝线击败了大部分的职业医生。

《科学》报道人工智能在心脏病预测上的应用，讲的是在Plos one上有一篇文章，机器可以预测有没有可能在近一段时间有心脏病的产生。

另外是新建并诊疗建议及监控，做得非常好的一个工作，用了千亿级的，只有400多张的影象，但是前面用大量的其他的正常的影像或者是其他疾病影像来做曲线，也是做得非常好。

脑瘤的术中快速诊断，用了新的技术可以适时的成像，用计算机快速的诊断，看看是不是有肿瘤，肿瘤的位置、大小等等。

神经假体的精确控制，4月份的一个报道，如果有肢体不健全的，他可能是装上义肢，之前很难分辨神经上的电流有什么样的动作，现在搞了50个传感器去搜集数据，然后进行分类，看最后到底这个电池是做一个什么样的运动。

Google使用人工智能程序检测组织切片乳腺癌，以及大小是什么。

中央台报道了人工智能三剑客，讲了浙江省人民亿元对肺结核的诊断。
</p>

<p>看过学术界的工作之后再看看工业界在玩什么？</p>

<p>阿里健康今年在深圳这边做云栖大会上讲阿里在做B超的诊断。百度有一个医疗大脑，今年整个部门移到了人工智能的部门里面去，在对话方面是做得比较好的。腾讯投了五个赛道各自的第一名，先面的还有好大夫，丁香园等等，把每个赛道最牛B的企业带起来了。</p>

<p>IBM做机器人问答，在诊断方面也做了相当大的工作，虽然最近从解约出来，但是目前在医学人工智能方面还是做得最好的企业之一，技术非常先进，是我们努力赶超的对象。

Google做了非常多的东西，健康，眼镜等等，通过眼镜可以实时监控去糖的含量。

苹果也做了大量的产品，包括手机里面有一个APP是跟北京宣武医院的合作。

梅西，是一个比较大的医联体，大量的数据来分享。

梅奥，从诊所做到研究院，非常的神奇，做一些适时的研究支持，提供更有价值的实验室服务。

凯撒医疗，是一个HMO，也做得非常好，控制成本为出发点，让病人不生病，跟病人自己的目标也是非常相近的，可以做到非常健康，形成较为连续的健康档案，有利于治疗和慢性病管理。

约翰.霍普金斯，把不同的整合到一家系统里面去。

克利夫兰诊所，做各种各样的工具。

加州大学洛杉矶分校医学中心也非常好，做了一些心率衰竭方面的指数的算法。

CVS和Walk—in，两家最大的医疗机构，可以拿到大量的用户的药方和处方，通过IBM模型做分析，让客户正好的掌握自己的健康状况。

Berg，把新药研发缩短非常多的时间。

Nuance，国内有很多，做语音识别的，麦克风也卖得非常非常好，类似这样的还可以收集大量的医生的数据，医生做手术的过程，大量的数据记录进去，都可以通过一款产品，感受特别好，对于治疗领域来说，有很多东西只要讲一下，就马上已经给你补全了。

Inovalon，这家公司也是收集了大量的医疗数据，做医疗整个环节中的优化。

UpToDate也是把大量的医疗文献进行理解，可以告诉医生有什么样的疾病，有什么样的临床路径会比较好。

Practo，医疗的管理软件，主要是帮助病人预料医生，做精准的医患品牌以匹配。

Zocdoc，也是做精准的医患匹配，也是目前美国最大的医患匹配的平台。

Patientslikeme，也是做病患的相似分析，看看哪些比较相近，拿治疗方案为自己服务。

保险公司，他可以预测哪些病人在未来三个月之内会出院，对那些潜在的入院的人群进行提醒，像心脏病衰竭的祝愿率下降到40%到50%。

SkinVision，他是比较聚焦于黑色素瘤，需要连续拍摄多张影像来判断。在国内百度也是做过类似这样的工作。

Neurotrack，看一张新、旧图片之间的视体验改变的状况。

AliveCor，中风和心率衰竭的情况。

23andme，做基因检测的公司，基于个体和家庭的基因情况进行精准的健康管理处方。

Cloudera，是一家做大数据的公司，做云计算的公司，他在这里也是做了一些医疗的，配合大型的医院，集团，预测病人入院的时间，调整资源分配。
</p>

<p>刚才已经看到了医学人工智能大概的现状。下面是一些展望。</p>

<p>医学人工智能的目标是让患者更了解自己，让医生更清楚患者，让医疗过程更加准确，医疗管理更加科学，第三方服务更加丰富。</p>

<p>用可视化的方式做一些简单的工具，让病人可以更加了解自己，让医生更加了解病人，通过多元的数据，相似的案例，个性化治疗方案的推进，让医生更了解患者，让医疗做得更精准，医学知识图谱，进行预测等等。人工智能做辅助的诊断，让医疗管理更加的科学，进行疾病的分析，疾病预测的趋势的分析，之前Google说它可以预测感冒流行，过两年之后又说不能预测感冒流行。</p>

<p>第三方服务更加丰富，有一个网站开放出来以后，当年的12月份出现了两百多家服务系统，比如说保险，所以我们认为第三方服务也会层出不穷。

对于我们来说做计算机的人怎么看？基本上是以检测量化，诊断评估，干预治疗作为换讯，我们基本上都在这里面。

可以做的是医学人工智能的辅助诊疗，比如说糖尿病的监测，类似于这样的辅助医生做一些工作。
</p>

<p>人工智能和大数据，都会有人问是不是能够替代医生，是不是以后医生要下岗，我想医生还是会持续的提供服务，我们做的人工智能的工具，大数据的工具做出来以后，给一些医生提供帮助。

还有医学上的研究，比如说医生推荐，医生到底擅长什么样的，可以看医学文献知道谁可能在这个方面更牛一些，还可以做医院的，疾病的，相关用药的知识图谱。还有是做非结构化的，文本的数据，或者是影像的数据做结构化。健康的预测，比如说拿到了大量病人的体检数据，从的实体状态来预测明年大概会变成什么样，明年的保费要增加到多少。</p>

<p>再做大数据监管，看看有没有异常行为，看看有没有谁是去医保骗保的，或者是做运营的，对用户进行画像，把合适的产品、合适的服务推给合适的用户，再做一些量身定制，做一些报告，辅助政策制定，比如说什么样的药应该进医保，什么样的药不能进医保。</p>

<p>最后是我们自己做的工作。</p>

<p>眼低图慢病检测云服务，从机器视觉的角度做了一下工作，并且取得了一系列的专利，最后是找到跟主动相关的系统病变，看看有没有，最后告诉你是不是可能在近三年类发生。这个服务已经放到了阿里云上。</p>

<p>深度学习的工具上也做了一些类似的，比如说糖尿病视网膜病变分析，跟google去年干的事情是一样的，他因为拥有更好的数据，所以他是业界做得最好的。我们也是拿到了公开数据上来做的，目前是70%左右的敏感度。</p>

<p>做医生擅长的，一部分是区分他的论文在哪些方面有贡献，另外是这个方向上进行排序，经过多轮迭代以后，最后可以对医生比较精确，排序也比较好，做到传统次数上的排行差不多。</p>

<p>甲状腺结节良恶性的预测，通过一些数据看这个结节到底是不是恶性的，恶性的程度有多大，是不是马上需要进行手术。

下面还拿到了挂号预约平台上大概三个月左右的数据，建立一个流转的过程，也可能那些病人到了C医生手里，要么被治好，要么被挂掉，这个医生是不是更牛逼。看就诊流程的预测，红色的是气温，绿色的是PM2.5，看看相关性，是不是哮喘和气温相关，还是跟空气质量更有关系。
</p>

<p>最后做疾病知识突破，对于TOP100的疾病来看，哪个疾病生了之后还有其他疾病的伴随，过了三年之后会不会有这样那样的疾病，这个也是让病人自己玩的。</p>

<p>浙大睿医研究中心是希望打造一个开放的平台，开放工具与数据，给大家使用。希望有更多的人一起加入到平台中来。

医学人工智能是非常好玩的领域，希望我们共襄盛举，谢谢大家。</p>
      </div>
      </div>

      <div v-if='newsbtn==2'>
      <p class="contenttit">浙大睿医糖网病AI研究取得突破成果 特异性达99%</p>
      <p class="ptime"><span>[央广网]</span><span style="margin-top: 1px;">2017-09-23</span></p>
      <img src="/static/image/news3.png" style="margin-bottom: 40px;" class="detailsimg">
      <div class="details">
        <p>央广网上海9月23日消息（记者杨静）记者昨天（22日）从第一届国际医疗人工智能大会上了解到，浙大睿医人工智能研究中心在糖尿病视网膜病变分级研究上取得突破性进展，特异性可达到99%。</p>
        <p>1.3亿糖尿病人，超过3000万糖网病（糖尿病视网膜病变）患者，失明危险比正常人高25倍，如何早期筛查眼底、快速诊断眼底图，成为防治糖网病的关键。浙大睿医人工智能研究中心主任吴健透露，浙大睿医在眼底病变检测方向取得了四项发明专利，同时与全国眼科学龙头北京同仁医院合作，AI深度学习后糖尿病视网膜病变5分类在大部分数据集上特异性可达到99%，敏感度达到95%。</p>
<p>今年三月份，浙大睿医人工智能研究中心于3月25日成立，集合了浙江大学计算机学院、信息学院、医学院、药学院、生仪学院等相关院系的科研力量，由中国人工智能科学带头人、浙江大学校长吴朝晖教授领衔建设，重点研究在临床大数据、影像学和基因组学上的医学人工智能共性关键技术。目前图像识别领域处于全国领先地位。</p>

<p>同时，浙大睿医创新“产学研”协作模式，与微医等公司合作，以产业反哺科研。浙大睿医的成果将率先让线上1.72亿实名用户、线下79个互联网医联体受益。</p>

<p>浙大睿医人工智能研究中心副主任吴福理向记者透露，我国优质医疗资源紧缺，AI工具可以在辅助诊断等方面协助医生诊疗：</p>

<p>“除了糖网病分级研究，浙大睿医在脑卒中眼底图慢病检测云服务、疾病知识图谱、骨龄图像识别和专业影像标注工具等研究领域均取得不错进展，预计未来两个月内会有重大产品发布。”</p>
      </div>
      </div>

      <div v-if='newsbtn==3'>
      <p class="contenttit">浙大睿医人工智能研究中心吴福理：我眼中医疗人工智能的现状</p>
      <p class="ptime"><span>[雷锋网]</span><span style="margin-top: 1px;">2017-10-31</span></p>
      <img src="/static/image/news4.png" style="margin-bottom: 40px;" class="detailsimg">
      <div class="details">
        <p style="text-indent: 0em">导语：资本大量涌入医疗人工智能领域，截至2017年8月份，根据亿欧智库的统计，国内医疗人工智能公司中有104家累积融资额已超过180亿人民币。</p>
        <p>资本大量涌入医疗人工智能领域，截至2017年8月份，根据亿欧智库的统计，国内医疗人工智能公司中有104家累积融资额已超过180亿人民币。</p>

<p>从这几个数字中也可看出当今医疗AI的火爆程度。那么在这个领域中到底什么才是最为关键的因素？医疗厂商、算法亦或是数据？</p>

<p>在近日由深圳市福田区人民政府指导，晶泰科技、腾讯主办，辉瑞制药、北京大学深圳研究院、科辉创新承办的2017人工智能＋医药健康创新峰会上，浙江大学睿医人工智能研究中心副主任吴福理谈了谈他的看法。</p>

<p>在吴福理看来，技术是提供给医生的工具，而非用来取代医生的。他认为AI最后的智慧会达到全人类整体的平均水平但不会超过人类，因为医疗是开放性问题，不像围棋属于封闭式问题。</p>

<p>除此之外，他还多次提到标准化数据的重要性。</p>

<p>吴福理表示，没有标准化的数据，AI就无从谈起。现阶段所有AI公司都在笼聚一些大型三甲医院，形成数据联盟。未来的医疗数据，尤其是临床数据，会像今天移动支付领域的数据一样，在大家使用后，国家会成立类似金融“网联”一样的医疗数据连接中心将这些数据聚集起来，并统一制定数据标准、标注标准，病种影像数据标准。</p>

<p>提及对医疗AI的展望，吴福理表示，关于服务医生、医院、用户端，所有的信息技术都会往云上走，往AI上靠。</p>  

<p>如今，所有医院的信息系统都在升级，通过传感器收集更多病人数据。人工智能最终是想让患者了解自己，让医生更了解患者，让医疗过程更加精准，让第三方服务更科学，医疗管理更科学。所有公司都会朝这个方向发展，不限于APP或PC，不限于移动还是非移动，不限于云端还是终端。</p>

<p>随后，吴福理也谈到了睿医人工智能研究中心，他们的愿景是以人工智能为翼助力医疗服务，从检测的量化到诊断评估到干预治疗。</p>

<p>以下是吴福理现场演讲文稿，雷锋网(公众号：雷锋网)做了不改变原意的修改与编辑：</p>

<p>我在浙大睿医人工智能研究中心任职，今天给大家分享下这几方面的经验：</p>

<p>第一是医学人工智能现状。第二是医学人工智能展望，有哪些领域容易突破，计算机技术能辅助医生做些什么？这个领域到底哪些组成比较关键？第三是谈谈浙大睿医人工智能研究中心的状况。</p>

<p>医学人工智能现状
从数据角度看，国家在这方面已有不少的布局和规划。关于人工智能的讨论，以“自动驾驶出了事故到底是谁负责”这一问题为例，国家已颁布了相关规范。那么在医疗人工智能领域，如果机器出了问题，到底是谁的责任？医生？机器？医院？人出错，人们往往可以能忍受，认为情有可原，但机器出错，很多人无法容忍。这其实是一个误解。</p>

<p>目前浙江省、福建、江西，已经有一定的标准去建立第三方影像中心。现在一个商用CT器械，如西门子的双源CT，拍一个人可以出2000张数据。而200张数据大概就200MB，如果一个医院一年数据达数十个TB，十个医院、一个城市的数据就是几个PB级，这么庞大的数据怎么分析？</p>
<p>如果有了足够的数据和明确规范，成立“国家队”机构也不无可能。</p>
<p>现在所有的AI公司都会笼聚一些大三甲医院，形成数据联盟。未来的医疗数据，尤其是临床的各种数据，会像今天移动支付领域的数据一样，在大家使用之后，国家会成立类似金融“网联”一样的医疗数据连接中心将这些数据聚集起来，形成数据标准、标注标准，病种影像数据标准。</p>
<p>我认为会朝这个方向发展。如今大大小小的公司都在不同领域解决医生、医院各种各样的痛点。会看到计算机领域大量创业公司利用大量APP抢不同的入口：院内、院外或者病人健康数据、医疗健康、诊断、医药，再到平时跟老百姓直接相关的。这些APP更多服务于老百姓，服务于医生群体的反而比较少。</p>
<p>现在大家都在讲用人工智能、大数据解决医院的问题。但实际现状是相关技术及设备只在北上广深、沿海城市有所应用，在西部甚至有的三乙医院连信息化都没有完全做到，数据都没有标准化。</p>
<p>没有标准化的数据，怎么做大数据AI？举例说，大家听到最多的就是让AI阅片。一个病人拍了双源CT，200张到2000张的数据，医生开始看片子，看到有病灶，开始写报告并给病人。这个数据对AI公司一点作用都没有，因为并不知道是在哪一张图片上面有病灶。
这里我们就会发现，现在数据非常稀缺，稀缺到AI公司会去大三甲医院花费100元买一张带标注的数据，光买数据就要花上千万。在这形势下，创业公司怎么跟BAT竞争。</p>
<p>再者就是数据安全问题。医疗上80%是影像数据，而影像数据一个案例都有几百兆。大部分人都在讲云端处理，我个人更倾向于数据，计算机的AI学习在云端，而最后的推理一定是在终端。</p>
<p>关于AI的工具，慢慢偏向于终端发展。我觉得在医疗领域，终端比云端更重要，因为云端是学习推理能力，未来如果数据标准化，所有AI公司都拿到，标准级数据不再那么贵时，你在终端的推理能力可能变得更加重要。当然，在中国把这个做框架推导的，还没有这样的实力。硅谷已经把AI学习框架全部公开。现在相对来说算法是比较平了，尤其是在工程领域。</p>
<p>下面讲讲几个巨头在医疗人工智能上的进展：</p>
<p>IBM Watson大家很熟悉，它像一个搜索引擎，更多做文本分析，但并没有太多的结合对应的各种影像学数据和病理学数据。</p>
<p>Google在糖网方向做得比较好，他们有很多数据是花钱购入的。医疗门槛比较高，最近一段时间大部分创业公司或者BAT开始买医疗数据，或者以联合建实验室的方式变相买数据。</p>
<p>从国内来说，腾讯在医疗领域布局比较多，全国有三家互联网医疗方面的公司走得比较前面，这三家公司全都得到了腾讯的投资。</p>
<p>与他们合作的医院我都会关心有没有结合医疗流程。对于医生或者病人来说，我并不介意是不是AI为我看的病，对医生来说更介意的是会不会影响正常的医疗流程。大部分做影像AI公司都会另做一套系统，而医生平时每天用的系统在中国是被上百家小厂商(HIS厂商、EMR厂商、PACS/RIS厂商、集成系统厂商等等)控制的，。你可以想象这样的AI落地有多困难。对医生来说，不可能今天在医院用那个系统，突然跳到另外一个系统用AI再来阅片有没有毛病，告诉我提示什么，我再反过来用现在的系统。</p>
<p>医学人工智能的展望
关于服务医生、医院、用户端，所有的信息技术都往云上走，往AI上靠。所有医院的信息系统在升级，通过传感器收集更多病人的数据。人工智能最终是想患者了解自己、医生更清楚患者、让医疗过程更加精准、让第三方服务更科学、医疗管理更科学。所有的公司都是朝这个方向发展，不限于用APP或者什么技术，是移动还是非移动，云端还是终端。</p>
<p>想要做好这五点，每一点都要投入大量的工作，当你收集足够多的数据后，就会有用户画像。一个人一生的数据沉淀不是在一个医院，而是在一个城市，从你在一个城市出生，妇产科、儿科再到普通科室。这些云如果能以城市级别的信息收集，个人的健康数据会更加全，对AI更有作用。</p>
<p>大家谈到的医药，新药实验，医学知识图谱，人工智能辅助分析，眼科。把这些管理全部数据化，当有大量数据时就可以做很多工作，比如最典型的医保欺诈。如果我知道城市级别整个医保数据，通过数据挖掘和可视化的手段就可以探测到欺诈，可视化领域不仅仅有这个作用。当然，医疗是其中一个应用而已。</p>
<p>做辅助诊断，拿睿医来说，因为我们是一个隶属于浙江大学的科研机构，我们的研究更多偏向于解决问题来医生辅助，不追逐市场热点。所以说，在甲状腺、前列腺、宫颈癌、肺这方面和医院合作会比公司稍微有优势，因为我们和医院科研合作方面可以得到更加高质量的数据。有数据显示我国医疗影像诊断错误的比例，在某些地区和某些诊断领域高达35%，个别基层医院的误诊率甚至更高。AI能提升到50%就非常有意义，因为能提升基层医疗医生的医疗水平。</p>
<p>健康方面的检测，比如医疗保险，这都是跟学术相关的，不做详细解读。</p>
<p>睿医人工智能研究中心
我们的愿景是人工智能助力医疗服务。在几个领域都已做了一些工作，从检测的量化到诊断评估到干预治疗。</p>
<p>第一件需要做的事是标准化的工具。我可以提供一个标准化工具让医生标注哪儿的数据有问题。再过两个月之后成立一个数据联盟，联合一些大的医院，形成一个标准化规范化标注结果。虽然你在各种医疗AI大赛看到一些数据，但那些影像的数据对放射科医生来说没多大作用，因为标注太简单了。比如肺结节，下面分六种类型，如果计算机没有足够的六种类型的详细标注数据，现在的深度学习技术不能精确判断，那么医生还要重新看，AI的实际作用大大打折扣。</p>
<p>可以做AR/VR工具，增强病患沟通。也可以做免费问诊，这里做一些问诊匹配，已经做得相对还可以。在TOP1能达到30%多。这个结果对全科医生很有用。做精准匹配，对病人来说口述是什么，搜索相关医生或者科室给他推荐。在一级科室分类准确率比较低，二级科室TOP5已经可以使用。</p>
<p>关于眼底检测，做到四方面专利。眼底检测可以判断一般患者80%的慢性疾病。如果通过非侵入式的检查患者的眼底，通云端或终端设备判断给出建议，这个比一般的侵入式检查好很多。
</p>
<p>视网膜病变，分辨率是90%，精确度已经达到75%。两年前我们做这件事的还不多，现在有不少公司在做。我们跟北京同仁医院合作了很久。</p>
<p>骨龄领域做了一些工作，骨龄是判断小孩发育成长快和慢的。如果这个小孩只有六岁，骨龄关节闭合程度达到7岁，就发育过快了。医生有一个图谱，收集大量图，会看两个图的对比。这个图谱是多少年来形成的，会有一定的误差，医生读片误差一般在一岁，而计算机误差在三个月以下。当然，有些科研数据已经精确到一个月了。有些领域是可以快速的做初筛，减少医生大量重复的劳动，整个提升医疗的水平。</p>
      </div>
      </div>

      <div v-if='newsbtn==4'>
      <p class="contenttit">首个医学人工智能联盟成立 浙大睿医将从四个方面予以支持</p>
      <p class="ptime"><span style="margin-top: 1px;">2017-11-21</span></p>
      <img src="/static/image/news5.png" style="margin-bottom: 40px;" class="detailsimg">
      <div class="details">
        <p>近日，首届国际智能医疗大会今天在杭州国际博览中心举行。会上，浙江大学牵头10家医院和相关机构发起了首个智能医疗行业联盟——医学人工智能联盟，浙大睿医人工智能研究中心成为联盟平台的技术支持单位。</p>
        <p>在此次会上，互联网医疗企业微医还发布了一项最新的成果：睿医智能医生。该成果是浙大睿医人工智能研究中心联合微医开发的西医人工智能诊疗应用。相对IBM的沃森和百度医疗大脑而言，睿医智能医生只能算是一个“入场新生”，但经过一年多对AI深度学习百万份级优质数据的深度学习，睿医智能医生在肺小结节、糖网病、宫颈癌筛查、骨龄检测、全科辅助诊断等十余个专科领域已实现关键突破。</p>
      </div>
      </div>
      <div v-if='newsbtn==5'>
      <p class="contenttit">2017中国卫生信息学会健康医疗大数据国际合作医学人工智能高峰论坛</p>
      <p class="ptime"><span style="margin-top: 1px;">2017-12-18</span></p>
      <img src="/static/image/news6.png" style="margin-bottom: 40px;" class="detailsimg">
      <div class="details">
        <p>2017中国卫生信息学会健康医疗大数据国际合作与交流年会暨医学人工智能高峰论坛，于2017年12月18日9：00，在圆正启真酒店3楼启真厅顺利召开。吴朝晖校长、姚建铨院士、廖洪恩教授、陈子仪教授，以及各位医学领导和专家，企业界领导和精英，高校各位领导和教授参与并指导了本次会议。</p>
        <p>本次会议提出要创建医学人工智能大联盟，联盟的愿景是做合作共享开放生态，希望是能够给大家提供一系列的服务，从项目的申报，平台数据流通基础的这个支撑平台，数据的标注，数据的标准化，还有一些专业的论坛等等去进行服务。首先是提供这个联盟内的开放平台，并提供了标记工具和可视化工具等一整套基础的设施给大家使用。未来联盟平台会对采集数据的管理，数据的标准，数据的安全等等提供一系列的平台支持。最后是能够把一系列的产品进行推广，打造基层智慧医疗的应用的一个商业模式。</p>
        <p>大会提出希望能够建立一个医生，数据，算法和场景的医疗大数据生态闭环，大家携起手来做出大数据银行，能够把数据的能力进行开放出来，鼓励技术和资本进入到我们的象牙塔里面，把我们的知识和能力释放出去，把中国的科技能力真正普及到所有的老百姓手里。医疗大数据跟医生的关系并不是完全取代的关系，而是为医生提供机器的效率，从而提升的医生的看病能力，人工智能跟医生的操作形成互补。</p>
        <p>大会指出人工智能、大数据，还有跟医疗的融合，是我们这个时代的召唤，也是我们社会的需求，良好的智能经济和智能社会可以推进未来经济和社会的高速的发展。</p>
      </div>
      </div>

       <div v-if='newsbtn==6'>
      <p class="contenttit">山东首家医学人工智能联合研究中心成立</p>
      <p class="ptime"><span>[人民政协网]</span><span style="margin-top: 1px;">2018-03-30</span></p>
      <img src="/static/image/news7.png" style="margin-bottom: 40px;" class="detailsimg">
      <div class="details">
        <p>人民政协网3月29日电（记者 李木元）3月29日，浙大睿医人工智能研究中心（下称“浙大睿医”）与青岛大学附属医院（下称“青大附院”）宣布合作成立医学人工智能联合研究中心，这是山东省首个由高校、医院合作成立的医学人工智能研究中心。</p>
        <p>据介绍，中心将围绕青大附院优势学科（专科）开展医学人工智能相关研究，探索眼科、肺小结节、胃癌病理、骨龄等研究成果在青大附院及其医联体的临床应用。青岛市西海岸新区人大副主任于惠娟，青大附院党委书记、理事长王新生，青大附院副院长牛海涛，乌镇互联网医院院长张群华，浙大睿医人工智能研究中心项目总监、浙江大学山东工业技术研究院医学人工智能研究中心副主任周小芬等领导、专家为中心揭牌。</p>
        <p>继2017年被写入国务院政府工作报告后，“人工智能”再入2018年政府工作报告。报告指出：发展壮大新动能。做大做强新兴产业集群，实施大数据发展行动，加强新一代人工智能研发应用，在医疗、养老、教育等多领域推进“互联网+”。2017年以来，人工智能在医疗领域的研发和应用投入达到前所未有的规模和高度。</p>
        <p>浙江大学在人工智能研究领域多年处于全国领先水平，计算机、软件两个学科均被评为“A+”，中心主任由浙江大学计算机学院教授、博导吴健担任，中心集聚了浙大在医、工、信领域的综合能力，目前已在二十多个医学专科领域取得关键性突破。</p>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 30px; display: block;">
        <img src="/static/image/detailscon1.png">
        <p style='text-align: center; font-size: 14px;'>领导专家为青岛大学附属医院智能医疗中心、互联网医联体揭牌</p>
        </div>
        <P>目前，浙大睿医在眼底疾病辅助检测系统通过一张眼底图可检测12种常见眼底疾病及病变位置，准确率达93%，二分类敏感度98%、特异性98.5%；肺小结节辅助诊断系统帮助医生快速定位病灶，平均召回率达到92%；阴道镜宫颈癌辅助诊断系统平均敏感度80%，达到三甲医院临床医生读片水平；胃癌病理辅助诊断系统识别准确率86.9%；儿童骨龄检测一岁以内准确率达93%。</P>
        <P>青大附院连续三年跻身“中国最佳医院百强榜”，是青岛市最大的省属综合性医院，专科实力强劲，拥有眼科、骨科等31个国家级、省级重点学科（专科）。自互联网、人工智能成为国家发展战略以来，青大附院积极探索互联网、人工智能技术在医疗领域的创新应用，在与微医合作落地“云、智、端”创新型医联体的同时，又与浙大睿医围绕31个重点专科开展联合研究。</P>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 30px; display: block;">
        <img src="/static/image/detailscon2.png">
        <p style='text-align: center; font-size: 14px;'>青岛大学附属医院远程心电中心</p>
        </div>
        <P>青大附院理事长王新生表示，青大附院门诊量居山东省首位，希望通过上述智能医疗成果在青大附院及其医联体落地，充分发挥青大附院的龙头作用，切实提高医联体内的医疗效率及专科水平，提升青岛“互联网+健康”的学科能力。</P>
        <p>周小芬表示，作为全国医学人工智能联盟的技术支持单位，浙大睿医将发挥“医学AI引擎”作用，向青大附院各专科全面开放计算、数据、模型、算法等AI服务工具以及应用能力，以循证医学为基础，研究出更多的医生 “助手”，共同推动中国医学人工智能的发展。</p>
      </div>
      </div>
      <div v-if='newsbtn==7'>
      <p class="contenttit">浙江省医疗数据产业研究会成立</p>
      <p class="ptime"><span style="margin-top: 1px;">2018-11-17</span></p>
      <img src="/static/image/news8.png" style=" width: 720px; margin-bottom: 40px;" class="detailsimg">
      <div class="details">
        <p>2018年11月15日，浙江省医疗数据产业研究会（以下简称“研究会”）第一次会员代表大会在浙江大学玉泉校区召开。自此，医学人工智能联盟正式纳入研究会，接受登记管理部门浙江省民政厅和业务主管部门浙江省科学技术协会的业务指导和监督管理。</p>
        <p style="font-weight: bold;">01 研究会第一次会员代表大会召开</p>
        <p>浙江省医疗数据产业研究会第一次会员代表大会由浙江大学计算机科学与技术学院吴健教授主持，在浙江省科学技术协会学会部部长戴力的监督指导下，宣读浙江省民政厅批复、浙江省科协批文，并汇报研究会筹备工作。</p>
        <img src="/static/image/detailscon5.png" class="detailscon">
        <p>会上，举手表决通过了研究会章程审议，无记名投票全票通过了研究会财务管理办法，并选举产生了首届理事会成员和研究会监事。理事会成员召开第一次理事会会议，无记名投票选举产生了理事长、副理事长、秘书长，以及党小组成员（组织架构名单见本文最后）。</p>
        <img src="/static/image/detailscon6.png" class="detailscon">
        <img src="/static/image/detailscon7.png" class="detailscon">
        <P>浙江省科学技术协会党组成员兼副主席姜长才在会上发表贺词，他指出党和国家高度重视健康医疗数据的应用，在全国多地建立了健康医疗数据中心。而浙江在这一方面尤有优势，为打造健康数据强省，建立浙江省医疗数据产业研究会尤为重要。同时，姜主席也对研究会未来发展提出了指导意见和建议。</P>
        <img src="/static/image/detailscon8.png" class="detailscon">
        <P>研究会首届理事长段树民院士发表讲话，代表研究会对现场来宾的参与和支持表示了感谢，并指出“我们将立足于浙江，着眼于全国，希望在浙江做好医疗数据的产学研，成为全国的典范，不断向全国推广。”</P>
        <img src="/static/image/detailscon9.png" class="detailscon">
        <P style="font-weight: bold;">02 2018中国医疗数据产业高峰论坛</P>
        <p>2018年11月16日，浙江省医疗数据产业研究会联合中国卫生信息与健康医疗大数据学会举办首届“中国医疗数据产业高峰论坛”。</p>
        <P>浙江大学校长吴朝晖院士、浙江省科学技术协会（浙江省医疗数据产业研究会业务主管单位）主席姚克，浙江省医疗数据产业研究会理事长段树民院士、中国卫生信息与健康医疗大数据学会会长金小桃等重要领导出席论坛，并发表重要讲话。</P>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon10.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>浙江大学校长吴朝晖院士致辞</p>
        </div>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon11.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>浙江省科协主席姚克致辞</p>
        </div>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon12.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>研究会理事长段树民院士致辞</p>
        </div>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon13.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>中国卫生信息与健康医疗大数据学会会长金小桃致辞</p>
        </div>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon14.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>浙江大学医学院附属第一医院院长王伟林演讲</p>
        </div>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon15.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>浙江大学医学院附属第二医院院长王建安演讲</p>
        </div>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon16.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>浙江大学医学院附属邵逸夫医院下沙院区党委副书记乔凯演讲</p>
        </div>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon17.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>院长论坛：吴健、陈旭东、陈新忠、邹朝春、刘云（从左往右）</p>
        </div>
        <P>本次论坛还设置了以数据标准、数据治理和数据应用为主题的三个分会场，分别由浙江大学医学院附属第一医院、浙江大学医学院附属第二医院和浙江大学医学院附属邵逸夫医院组织承办，来自包括国家卫健委相关领导、国家药品监督管理局、中国食品药品检定研究院相关领导等参与解读相关领域政策；来自包括高校生物仪器、计算机、生物医学工程、大数据智能、转化医学等方面负责人、专家，以及大数据、人工智能领域内的领军型企业等40多位专家参与交流国内外最新研究动态，共同探讨推动医疗数据产业、推进智能健康中国建设。</P>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon18.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>浙一专场：数据标准</p>
        </div>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon19.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>浙二专场：数据治理</p>
        </div>
        <div style=" width: 600px; height: 430px; margin: 0 auto; margin-top: 20px; margin-bottom: 20px; display: block;">
        <img src="/static/image/detailscon20.png" class="detailscon" style="margin-bottom: 0">
        <p style='text-align: center; font-size: 14px;'>邵医专场：数据应用</p>
        </div>
        <P style='font-weight: bold;'>03 浙江省医疗数据产业研究会组织架构</P>
        <P>理事长：段树民</P>
        <P>副理事长：蔡秀军、柯越海、梁廷波、王建安、吴健（按姓氏拼音排序）</P>
