        <p style="font-weight: bold;">05研究会首届会员单位名单（部分）</p>
        <p>浙江工业大学、余杭区第二人民医院、浪潮集团、阿斯利康制药、迪安诊断、方回春堂、西贵资本、浙江联众、浙江安贞医疗、浙江立视通、新译科技、枫业医疗科技、兴明医疗器材、同科林医疗仪器、网优健康、每天健康科技、医好网络科技、医哲医疗科技、惠医科技、特杨网络科技、网新恩普、惠优科技、火石创造、瀚举医疗科技、湖州爱尔眼科医院等70多家机构及企业代表。</p>
        <p>研究会将以浙江大学及相关核心大三甲医院为平台建设基础，服务辐射至全国各地医疗机构、高校、科研单位及相关企事业单位。研究会将致力于降低医学人工智能行业门槛，推动医疗科技转化，促进医疗数据流通。研究会的成立有利于通过平台提供数据服务，保障数据安全，规范数据价值挖掘，推动医疗数据标准化建设，加速医疗科技创新和价值转化，最终助推健康医疗产业的有序发展，以及医疗数据产业、医疗人工智能交叉人才的培养。</p>
        <p>千里之行，始于足下。秉承初心，携手合作，“集大家之所成”，充分发挥高端专家优势，不断提供高质量科技类公共服务产品，坚守服务精神，提升研究会凝聚力。</p>
