      <div v-if='newsbtn==8'>
      <p class="contenttit">温州市人民医院牵手浙大睿医人工智能 助力精准医疗服务</p>
      <p class="ptime"><span style="margin-top: 1px;">2019-03-15</span></p>
      <img src="/static/image/news9.png" style="margin-bottom: 40px;" class="detailsimg">
      <div class="details">
        <p>人工智能在医院可以做什么？温州市人民医院将给出答案。3月15日，温州市人民医院、浙大睿医人工智能研究中心、微医集团三方签订合作框架协议，温州市人民医院正式成为浙大睿医人工智能应用示范基地。</p>
        <p>紧缺的优质医疗资源与疑难杂症如何相互匹配这一问题，如今正在市人民医院逐渐实现，并成为医院门诊管理的有效探索。患者只需要通过描述自己的病症，AI就能给出准确的就诊意见。例如挂哪个科、哪个科医生，告知预测的疾病。减少了很多因为过错号而浪费时间、精力的事情。这一做法也将有效打击“号贩子”现象，实现患者和专家的“精准匹配”，让那些真正需要“大牌”专家诊治的患者无需等候数周，第一时得到救治。</p>
        <p>对医生来说，AI工具则能为医生提供诊疗辅助提示，在一定程度上提高医生的工作效率，减轻医生的负担。通过把专业数据源源不断地输入进去，以循证医学为基础，不断训练、不断改良，最终成为医生的专业助手。通过AI技术的落地，温州人民医院将切实提升自身的运营管理能力，增强技术实力，梳理医院数据质量。</p>
        <p>同时，这种人工智能的辅助诊断应用模式，可以复制扩展到妇产科、慢病等其他疾病领域，为进一步健全“互联网+医疗健康”服务体系、完善智慧医疗产业结构提供有力助推，实现对医疗资源的公平调配。</p>
        <P>市人民医院有关人员表示，本次签约合作，借助温人医、浙大睿医和微医三方在医学、临床、信息化技术和医学人工智方面的研究基础，开发能综合辅助医生疾病诊断的温州市人民医院医学人工智能云平台，将使医院及基层医院接诊能力和诊疗水平大幅提升，有效提升患者的诊疗质量，提高健康保障水平。</P>
      </div>
      </div>

      <div v-if='newsbtn==9'>
      <p class="contenttit">武汉一医牵手浙大睿医，共建医学人工智能中心</p>
      <p class="ptime"><span>[新浪湖北]</span><span style="margin-top: 1px;">2019-07-04</span></p>
      <img src="/static/image/news10.png" style="margin-bottom: 40px;" class="detailsimg">
      <div class="details">
        <p>本报讯（记者通讯员喻玲玲）医疗健康是人工智能与大数据应用的重要领域，医疗与人工智能技术的融合将成为未来医学发展的重要方向。7月4日，武汉市第一医院与浙江大学睿医人工智能研究中心签署战略合作协议。浙大睿医在武汉市第一医院，挂牌成立医学人工智能及大数据应用联合研究中心、人工智能及大数据应用示范基地，并在此基础上深化人工智能在临床专科上的研究与应用，双方共同成立皮肤病人工智能联合实验室和脑卒中人工智能联合实验室。</p>
        <p>签约揭牌仪式上，武汉市第一医院院长魏力和浙江大学睿医人工智能研究中心主任吴健代表双方签订合作协议。武汉市卫健委副主任魏善波、湖北省卫健委信息中心党支部书记肖树发、华中科技大学同济医学院医药卫生管理学院院长冯占春等出席了签约揭牌仪式。</p>
        <p>据了解，此次“结盟”可谓是强强联合。浙江大学睿医人工智能研究中心作为人工智能研究的先行者，发挥浙大计算机学院、信息学院、医学院、药学院等院系在医学人工智能领域的技术优势，创建出中国自己的医学人工智能开放平台。而武汉市第一医院的信息化建设已经达到国家标准，是全国首家通过国家医疗健康信息互联互通标准化认证的中西医结合医院，医院被评为“国家远程医疗与互联网医学中心武汉协同中心”，已具备了发展人工智能良好的基础条件，同时该院学科建设齐全，有着较好地科研和临床业务支撑，人工智能可在多个环节发挥出潜力与优势。</p>
        <p>根据合作协议，双方将展开深度合作，在共同成立“医学人工智能及大数据应用联合研究中心”的基础上，深化人工智能在临床专科上的研究与应用，成立“皮肤病人工智能联合实验室”和“脑卒中人工智能联合实验室”。</p>
        <P>武汉市第一医院皮肤科是中部地区皮肤病区域诊疗中心，也是享誉全国的国家级重点专科，年门诊量过百万人次。成立“皮肤病人工智能联合实验室”后，双方将探索实现针对皮肤病的人工智能诊断辅助系统，开展对红斑鳞屑性疾病（如银屑病）、大疱性疾病及色素性皮肤病等疾病的诊断、治疗及预后评估研究工作。</P>
        <p>脑卒中已经成为严重威胁国人健康的“杀手”之一。武汉市第一医院在卒中急救中打造出一整套独特的“卒中急救模式”已叫响全国，院也被国家卫生健康委脑防委评选为国家“示范卒中防治中心”。双方成立“脑卒中人工智能联合实验室”，将人工智能应用在卒中识别与预防上，探索脑血管病人工智能诊断及治疗计划制定，向患者提供卒中后抑郁的预测与诊断、卒中后康复建议等智能服务。</p>
        <p>“期望能在医疗AI领域深耕技术创新与应用，转变并优化医疗手段与模式，未来能够成为医疗智能领域的领航者。”武汉市第一医院院长魏力表示，此次合作是建立多方、多维度的全新合作模式，医院将与浙大睿医一起研究并明确各临床科室和信息工作之间任务分工，把相关工作流程化、常态化。同时，将人工智能与大数据技术融入到该院“互联网医院”建设中，最终建成集互联网医院、医联体、专病质控网络于一体的三网合一的诊疗体系，让人工智能赋能我院智慧医疗，提高广大患者的获得感，让医院信息化造福社会、造福于民。</p>
      </div>
      </div>


    </div>
  </div>
  </div>
  </div>
  </div>
