<template>
  <div class="main">
 <div class="block">
  <div class="titlerelative">
    <img src="/static/image/aboutus.jpg">
    <div class="toptitle">
      <p>关于我们</p>
      <span>ABOUT US</span>
    </div>
    </div>
  </div>
  <div class="aboutbg">
   <div class="aboutmain">
        <p class="ptitle"><img src="/static/image/titleicon.png" class="titleimg"><span>中心背景</span></p>
        <div class="bgmain"><p>浙大睿医人工智能研究中心（以下简称“中心”），成立于2017年3月，坐落于杭州市西湖区，中心致力于引领医学领域人工智能技术突破，通过多学科合作、产学研联动的方式，助力医学人工智能产业发展。研究中心团队有来自IEEE Fellow、ACM Fellow、清华大学、浙江大学等著名研究机构的博士科学家，以及来自谷歌、微软、阿里、百度等产业界的专业人才。中心主要目标是建立一个开放型的产学研协同创新平台，创建中国自己的医学人工智能开放平台，助力我国医疗健康数据挖掘、应用及安全保障；主要研究内容包括：医学影像重建、医学影像生成、医学影像追踪、医学影像配准、智能标注、智能检测、智能分割、多模态融合技术等等。</p>
<p>中心面向全国乃至全球医疗机构和学科带头人开放技术和应用合作，实现与医疗机构诊疗经验、学术研究、临床数据的对接，寻求科研成果向产业成果的转化，并以产业发展反哺科研工作，实现产学研的协同。</p>
<p>目前中心已研发眼底疾病、宫颈癌、肺癌AI筛查系统、胃癌病理、儿童骨龄AI辅助诊断系统等多个产品；已经在浙一、浙二、邵逸夫、青医附院、郑大一附院等20余家三甲医院开展示范应用，智能分诊、全科医生等产品在微医集团、飞图影像等企业应用；申请30余项国家发明专利，搭建医学人工智能开放平台，提供标注、可视化、建模工具。此外，中心还与哈佛大学联合研究AI全科医生系统、与UCLA、UCSD、耶鲁、香港科技大学等合作研究专病AI系统；与北京同仁、郑州一附院、浙一、浙二、邵逸夫、省妇保、省儿保等医院合作研究专病AI系统；与理邦、上邦、江丰等硬件企业联合研发AI赋能的新硬件；与百度、阿里健康、腾讯、微医、复兴、浪潮等企业共同开展应用示范。</p>
   </div>
   </div>  
  </div>
</div>
</template>

<script>
export default {
  name: 'research',
  data () {
    return {
      showmore:'',
      showbtn1:false,
      showbtn2:false,
      showbtn3:false,
      showbtn4:false,
      showbtn5:false,
      showbtn6:false,
      showbtn7:false,
    }
  },
  methods:{
      more:function(e){
        if(e==0){
         this.showbtn1=!this.showbtn1
        }
        else if(e==1){
         this.showbtn2=!this.showbtn2
        }
        else if(e==2){
         this.showbtn3=!this.showbtn3
        }
        else if(e==3){
         this.showbtn4=!this.showbtn4
        }
        else if(e==4){
         this.showbtn5=!this.showbtn5
        }
        else if(e==5){
         this.showbtn6=!this.showbtn6
        }
        else if(e==6){
         this.showbtn7=!this.showbtn7
        }
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

