.ptitle{ font-family: PingFangSC-Semibold;font-size: 21px;color: #011F90;text-align: justify;line-height: 22px; font-weight: 600; margin-bottom: 24px; }
.ptitle span{ display: inline-block; vertical-align: top; line-height: 22px;}
.bgmain{font-family: PingFangSC-Regular; padding-bottom: 130px;
font-size: 14px;
color: #5B5B5D;
text-align: justify;
line-height: 32px;}
.bgmain p{ padding-bottom: 14px; }
.ulmain{ text-align: left; padding-bottom: 70px; }
.ulmain li{ width:570px;  padding: 40px; box-sizing: border-box; display: inline-block;vertical-align: top; background: #FFF; margin-bottom: 30px; position: relative; }
.ulmain li:nth-child(odd){ margin-right: 55px }
.more{ position: absolute; font-family: PingFangSC-Regular;font-size: 14px;color: #5B5B5D;text-align: right;line-height: 14px; bottom: 20px; right: 47px; cursor: pointer; }
.imgpic{ width: 120px; height: 120px;display: inline-block; margin-right: 26px; }
.rightintroduce{ display: inline-block;vertical-align: top; width: 330px; }
.title{font-family: PingFangSC-Semibold; margin-bottom: 10px; }
.name{ font-size: 22px;color: #3F80FF;line-height: 22px; display: inline-block;vertical-align: top;}
.post{font-size: 16px;color: #3F80FF;line-height: 16px; margin-left: 8px; display: inline-block;vertical-align: bottom;}
.qualifications{font-family: PingFangSC-Medium;font-size: 15px;color: #525C70;text-align: justify; font-weight: 600; line-height: 15px; margin-bottom: 16px; }
.uscontent{ width: 330px;  height: auto;  max-height: 132px;  transition: max-height 1s;
display: -webkit-box;
-webkit-box-orient: vertical;
-webkit-line-clamp: 6;
overflow: hidden;
font-family: -webkit-pictograph;font-size: 14px;color: #525C70;text-align: justify;line-height: 22px; }

.titlerelative{position: relative; width: 1920px;margin: 0 auto;}
.toptitle{ position: absolute;text-align: left; left: 15.4%; top: 240px; }
.toptitle p{font-family: PingFangSC-Medium;
font-size: 32px;
color: #FFFFFF;
letter-spacing: 1px;
line-height: 32px;margin-bottom: 10px;}
.toptitle span{font-family: PingFangSC-Regular; font-weight: 100;
font-size: 16px;
color: #FFFFFF;
line-height: 16px;}
.showmore{ -webkit-line-clamp: 30;max-height: 500px; height: auto; }
.titleimg{ width: 18px; height: 18px; display: inline-block;vertical-align: middle;margin-right: 4px; margin-top: -1px; }
</style>


