<template>
	<div class="mainbtn"><div class="leftimg"><img src="/static/image/toplogo.png"></div><div class="switchlang"><span class="active">中文</span><span>EN</span></div><ul class="ultopbtn"><li><router-link :to="{path:'/index'}" class='topfont'>首页</router-link></li><li><router-link :to="{path:'/research'}" class='topfont'>研究合作</router-link></li><li><router-link :to="{path:'/news'}" class='topfont'>新闻资讯</router-link></li><li><router-link :to="{path:'/recruit'}">人才招聘</router-link></li><li><router-link :to="{path:'/about'}" class='topfont'>关于睿医</router-link></li></ul></div>
</template>
<script>
export default {
  name: 'top',
  data () {
    return {
    }
  }
}
</script>
<style scoped>
.mainbtn{ width: 1920px; position: absolute; margin-left:-960px; left: 50%; padding: 0 20px; box-sizing: border-box; background-color:rgba(0,0,0,0); height: 80px;  z-index: 10; padding-right: 50px; }
.leftimg{ width: 350px; height: 80px; display: inline-block;float: left; margin-left: 30px;}
.ultopbtn{ float: right;height: 80px; display: inline-block; line-height: 80px;}
.ultopbtn li{ width: 90px; margin-right: 50px; display: inline-block; font-family: PingFangSC-Regular;
font-size: 19px;
letter-spacing: 0;
text-align: center;}
.ultopbtn li a{color: #AFB7C4; }
.switchlang{float: right; height: 80px; display: flex;  font-size: 12px;
color: #FFFFFF;
letter-spacing: 0;
text-align: center;
line-height: 20px;}
.switchlang .active{ background: #FFFFFF;
border-radius: 4px 0 0 4px; color: #4178FF; }
.switchlang span{border: 1px solid #FFFFFF;border-radius: 0 4px 4px 0; width: 36px; height: 20px; margin: auto;}
.ultopbtn li .active{ color: #fff; }
</style>



